
'use client';

import { useEffect, useState, useTransition } from 'react';
import { useForm, Controller } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { getUserHeaderData } from '@/lib/actions';
import type { HeaderData } from '@/lib/schemas';
import { HeaderDataSchema } from '@/lib/schemas';
import { useToast } from '@/hooks/use-toast';
import { Loader2 } from 'lucide-react';

interface HeaderContentProps {
  refreshKey: number;
  initialFetchComplete: boolean;
  setInitialFetchComplete: (value: boolean) => void;
  headerProfiles: HeaderData[];
  setHeaderProfiles: (profiles: HeaderData[]) => void;
  selectedProfile: HeaderData | null;
  setSelectedProfile: (profile: HeaderData | null) => void;
}

export function HeaderContent({ 
  refreshKey, 
  initialFetchComplete, 
  setInitialFetchComplete,
  headerProfiles,
  setHeaderProfiles,
  selectedProfile,
  setSelectedProfile
}: HeaderContentProps) {
  const { toast } = useToast();
  const [isPending, startTransition] = useTransition();

  const { control, setValue, reset } = useForm<HeaderData>({
    resolver: zodResolver(HeaderDataSchema),
    defaultValues: {
      tpTIN: '',
      branchCode: '',
      rdoCode: '',
      entityType: '',
      cycleType: 'calendar',
      monthSelect: 'december',
      companyName: '',
      lastName: '',
      firstName: '',
      middleName: '',
      tradeName: '',
      subStreet: '',
      street: '',
      barangay: '',
      cityMunicipality: '',
      province: '',
      zipCode: '',
    },
  });

  useEffect(() => {
    const fetchData = () => {
        startTransition(async () => {
            const databaseId = localStorage.getItem('databaseId');
            if (databaseId) {
                const result = await getUserHeaderData(databaseId);
                if (result.success && result.data) {
                    setHeaderProfiles(result.data as HeaderData[]);
                    if (refreshKey > 0) {
                        toast({
                            title: 'Refresh Successful',
                            description: 'The data has been updated.',
                        });
                    }
                } else {
                    toast({
                        title: 'Error',
                        description: result.error || 'Failed to fetch header data.',
                        variant: 'destructive',
                    });
                }
            }
            if (!initialFetchComplete) {
                setInitialFetchComplete(true);
            }
        });
    };

    if (!initialFetchComplete || refreshKey > 0) {
        fetchData();
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [refreshKey]);

  useEffect(() => {
    if (selectedProfile) {
      Object.keys(selectedProfile).forEach(key => {
        setValue(key as keyof HeaderData, selectedProfile[key as keyof HeaderData]);
      });
    } else {
      reset();
    }
  }, [selectedProfile, setValue, reset]);

  const handleTinSelect = (tin: string) => {
    const profile = headerProfiles.find(p => p.tpTIN === tin);
    setSelectedProfile(profile || null);
  };

  return (
    <div className="p-6 bg-white border-l h-full">
      <div className="flex items-center justify-between pb-4 border-b">
        <div className="flex items-center gap-4">
          <Label htmlFor="tin-select" className="text-sm font-medium">Load TIN:</Label>
          {isPending && !initialFetchComplete ? (
            <div className="flex items-center gap-2">
              <Loader2 className="h-4 w-4 animate-spin" />
              <span>Loading TINs...</span>
            </div>
          ) : (
            <Select onValueChange={handleTinSelect} disabled={isPending} value={selectedProfile?.tpTIN || ""}>
              <SelectTrigger id="tin-select" className="w-[180px]">
                <SelectValue placeholder="Select TIN" />
              </SelectTrigger>
              <SelectContent>
                {headerProfiles.map(profile => (
                  <SelectItem key={profile.tpTIN} value={profile.tpTIN}>{profile.tpTIN}</SelectItem>
                ))}
              </SelectContent>
            </Select>
          )}
        </div>
        <Button>Save Header</Button>
      </div>

      <div className="mt-6">
        <h2 className="text-xl font-bold text-center mb-6">Taxpayer Information</h2>
        <div className="grid grid-cols-1 gap-8 md:grid-cols-3">
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Tax ID and Other Details</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="tin">Tax Identification Number</Label>
                <Controller name="tpTIN" control={control} render={({ field }) => <Input id="tin" {...field} />} />
              </div>
              <div className="space-y-2">
                <Label htmlFor="branch-code">Branch Code</Label>
                <Controller name="branchCode" control={control} render={({ field }) => <Input id="branch-code" {...field} />} />
              </div>
              <div className="space-y-2">
                <Label htmlFor="rdo-code">RDO Code</Label>
                <Controller name="rdoCode" control={control} render={({ field }) => <Input id="rdo-code" {...field} />} />
              </div>
              <div className="space-y-2">
                <Label htmlFor="classification">Classification</Label>
                <Controller
                  name="entityType"
                  control={control}
                  render={({ field }) => (
                    <Select onValueChange={field.onChange} value={field.value}>
                      <SelectTrigger id="classification">
                        <SelectValue placeholder="Select Classification" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="Non-Individual">NON-INDIVIDUAL</SelectItem>
                        <SelectItem value="Individual">INDIVIDUAL</SelectItem>
                      </SelectContent>
                    </Select>
                  )}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="reporting-cycle">Reporting Cycle</Label>
                <Controller
                  name="cycleType"
                  control={control}
                  render={({ field }) => (
                    <Select onValueChange={field.onChange} value={field.value}>
                      <SelectTrigger id="reporting-cycle">
                        <SelectValue placeholder="Select Cycle" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="calendar">CALENDAR</SelectItem>
                        <SelectItem value="fiscal">FISCAL</SelectItem>
                      </SelectContent>
                    </Select>
                  )}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="fiscal-month">Fiscal Month</Label>
                <Controller
                  name="monthSelect"
                  control={control}
                  render={({ field }) => (
                    <Select onValueChange={field.onChange} value={field.value}>
                      <SelectTrigger id="fiscal-month">
                        <SelectValue placeholder="Select Month" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="1">JANUARY</SelectItem>
                        <SelectItem value="2">FEBRUARY</SelectItem>
                        <SelectItem value="3">MARCH</SelectItem>
                        <SelectItem value="4">APRIL</SelectItem>
                        <SelectItem value="5">MAY</SelectItem>
                        <SelectItem value="6">JUNE</SelectItem>
                        <SelectItem value="7">JULY</SelectItem>
                        <SelectItem value="8">AUGUST</SelectItem>
                        <SelectItem value="9">SEPTEMBER</SelectItem>
                        <SelectItem value="10">OCTOBER</SelectItem>
                        <SelectItem value="11">NOVEMBER</SelectItem>
                        <SelectItem value="12">DECEMBER</SelectItem>
                      </SelectContent>
                    </Select>
                  )}
                />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Registered Name</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="company-name">Company Name</Label>
                <Controller name="companyName" control={control} render={({ field }) => <Input id="company-name" {...field} />} />
              </div>
              <div className="space-y-2">
                <Label htmlFor="last-name">Last Name</Label>
                <Controller name="lastName" control={control} render={({ field }) => <Input id="last-name" {...field} />} />
              </div>
              <div className="space-y-2">
                <Label htmlFor="first-name">First Name</Label>
                <Controller name="firstName" control={control} render={({ field }) => <Input id="first-name" {...field} />} />
              </div>
              <div className="space-y-2">
                <Label htmlFor="middle-name">Middle Name</Label>
                <Controller name="middleName" control={control} render={({ field }) => <Input id="middle-name" {...field} />} />
              </div>
              <div className="space-y-2">
                <Label htmlFor="trade-name">Trade Name</Label>
                <Controller name="tradeName" control={control} render={({ field }) => <Input id="trade-name" {...field} />} />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Registered Address</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="sub-street">Sub-street</Label>
                <Controller name="subStreet" control={control} render={({ field }) => <Input id="sub-street" {...field} />} />
              </div>
              <div className="space-y-2">
                <Label htmlFor="street">Street</Label>
                <Controller name="street" control={control} render={({ field }) => <Input id="street" {...field} />} />
              </div>
              <div className="space-y-2">
                <Label htmlFor="barangay">Barangay</Label>
                <Controller name="barangay" control={control} render={({ field }) => <Input id="barangay" {...field} />} />
              </div>
              <div className="space-y-2">
                <Label htmlFor="city">City/Municipality</Label>
                <Controller name="cityMunicipality" control={control} render={({ field }) => <Input id="city" {...field} />} />
              </div>
              <div className="space-y-2">
                <Label htmlFor="province">Province</Label>
                <Controller name="province" control={control} render={({ field }) => <Input id="province" {...field} />} />
              </div>
              <div className="space-y-2">
                <Label htmlFor="zip-code">Zip Code</Label>
                <Controller name="zipCode" control={control} render={({ field }) => <Input id="zip-code" {...field} />} />
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
