import { z } from 'zod';

export const HeaderDataSchema = z.object({
  tpTIN: z.string(),
  branchCode: z.string(),
  rdoCode: z.string(),
  entityType: z.string(),
  cycleType: z.string(),
  monthSelect: z.string(),
  companyName: z.string(),
  lastName: z.string(),
  firstName: z.string(),
  middleName: z.string(),
  tradeName: z.string(),
  subStreet: z.string(),
  street: z.string(),
  barangay: z.string(),
  cityMunicipality: z.string(),
  province: z.string(),
  zipCode: z.string(),
});

export type HeaderData = z.infer<typeof HeaderDataSchema>;
