'use server';

import { excelErrorDetection } from '@/ai/flows/excel-error-detection';
import type { ExcelErrorDetectionOutput } from '@/ai/schemas';
import { ExcelErrorDetectionOutputSchema } from '@/ai/schemas';
import { z } from 'zod';
import { appendUserToSheet, getAllUsers, getAllHeaderDataFromSheet, type SignupData, type LoginData } from './googlesheets';
import { type HeaderData } from './schemas';

const AnalyzeFileResultSchema = z.object({
  success: z.boolean(),
  data: ExcelErrorDetectionOutputSchema.nullable(),
  error: z.string().nullable(),
});

type AnalyzeFileResult = z.infer<typeof AnalyzeFileResultSchema>;

export async function analyzeExcelFile(
  formData: FormData
): Promise<AnalyzeFileResult> {
  const file = formData.get('file') as File | null;

  if (!file) {
    return { success: false, data: null, error: 'No file uploaded.' };
  }

  // Check file type
  if (file.type !== 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' && file.type !== 'application/vnd.ms-excel') {
    return { success: false, data: null, error: 'Invalid file type. Please upload an Excel file.' };
  }
  
  try {
    const bytes = await file.arrayBuffer();
    const buffer = Buffer.from(bytes);
    const dataUri = `data:${file.type};base64,${buffer.toString('base64')}`;

    const result = await excelErrorDetection({ excelDataUri: dataUri });

    return { success: true, data: result, error: null };
  } catch (e) {
    console.error(e);
    const errorMessage = e instanceof Error ? e.message : 'An unknown error occurred.';
    return { success: false, data: null, error: `Failed to analyze file: ${errorMessage}` };
  }
}

const AuthResultSchema = z.object({
    success: z.boolean(),
    error: z.string().nullable(),
    user: z.object({
        userName: z.string(),
        databaseId: z.string().optional(),
    }).nullable(),
});

type AuthResult = z.infer<typeof AuthResultSchema>;

export async function signUpUser(userData: SignupData): Promise<AuthResult> {
  try {
    await appendUserToSheet(userData);
    return { success: true, error: null, user: null };
  } catch (e) {
    console.error('Error signing up user:', e);
    const errorMessage = e instanceof Error ? e.message : 'An unknown error occurred.';
    return { success: false, error: `Failed to sign up: ${errorMessage}`, user: null };
  }
}

export async function loginUser(credentials: LoginData): Promise<AuthResult> {
    try {
        const users = await getAllUsers();
        const user = users.find(u => u.userName === credentials.userName && u.pwd === credentials.password);

        if (user) {
            return { success: true, error: null, user: { userName: user.userName, databaseId: user.databaseId } };
        } else {
            return { success: false, error: 'Invalid username or password.', user: null };
        }
    } catch (e) {
        console.error('Error logging in user:', e);
        const errorMessage = e instanceof Error ? e.message : 'An unknown error occurred.';
        return { success: false, error: `Failed to login: ${errorMessage}`, user: null };
    }
}

const HeaderDataResultSchema = z.object({
  success: z.boolean(),
  data: z.array(z.any()).nullable(), // Using any for now, will be HeaderData schema
  error: z.string().nullable(),
});
type HeaderDataResult = z.infer<typeof HeaderDataResultSchema>;

export async function getUserHeaderData(databaseId: string): Promise<HeaderDataResult> {
  try {
    if (!databaseId) {
      return { success: false, data: null, error: 'Database ID is required.' };
    }
    const headerData = await getAllHeaderDataFromSheet(databaseId);
    return { success: true, data: headerData, error: null };
  } catch (error) {
    console.error('Error fetching header data:', error);
    const errorMessage = error instanceof Error ? error.message : 'An unknown error occurred.';
    return { success: false, data: null, error: `Failed to fetch header data: ${errorMessage}` };
  }
}