
'use client';

import { useState } from 'react';
import Link from 'next/link';
import { LogOut, FileText, LayoutTemplate, ChevronsRightLeft, Folder, History, RefreshCw } from 'lucide-react';
import { Logo } from '@/components/logo';
import { Button } from '@/components/ui/button';
import {
  Sidebar,
  SidebarContent,
  SidebarHeader,
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
  SidebarProvider,
  SidebarTrigger,
  SidebarInset,
} from '@/components/ui/sidebar';
import { HeaderContent } from '@/components/header-content';
import { useToast } from '@/hooks/use-toast';
import type { HeaderData } from '@/lib/schemas';

const TemplateContent = () => <div className="p-4">Template Content</div>;
const ConvertContent = () => <div className="p-4">Convert Content</div>;
const FormsContent = () => <div className="p-4">Forms Content</div>;
const DatFilesContent = () => <div className="p-4">DAT Files Content</div>;


export default function DashboardPage() {
  const [activeTab, setActiveTab] = useState('Header');
  const [refreshKey, setRefreshKey] = useState(0);
  const { toast } = useToast();
  const [initialFetchComplete, setInitialFetchComplete] = useState(false);
  const [headerProfiles, setHeaderProfiles] = useState<HeaderData[]>([]);
  const [selectedProfile, setSelectedProfile] = useState<HeaderData | null>(null);

  const handleRefresh = () => {
    setRefreshKey(prevKey => prevKey + 1);
  };

  const renderContent = () => {
    switch (activeTab) {
      case 'Header':
        return <HeaderContent 
                  refreshKey={refreshKey} 
                  initialFetchComplete={initialFetchComplete} 
                  setInitialFetchComplete={setInitialFetchComplete}
                  headerProfiles={headerProfiles}
                  setHeaderProfiles={setHeaderProfiles}
                  selectedProfile={selectedProfile}
                  setSelectedProfile={setSelectedProfile}
                />;
      case 'Template':
        return <TemplateContent />;
      case 'Convert':
        return <ConvertContent />;
      case 'Forms':
        return <FormsContent />;
      case 'DAT Files':
        return <DatFilesContent />;
      default:
        return <HeaderContent 
                  refreshKey={refreshKey} 
                  initialFetchComplete={initialFetchComplete} 
                  setInitialFetchComplete={setInitialFetchComplete}
                  headerProfiles={headerProfiles}
                  setHeaderProfiles={setHeaderProfiles}
                  selectedProfile={selectedProfile}
                  setSelectedProfile={setSelectedProfile}
                />;
    }
  };

  return (
    <div className="flex min-h-screen bg-gray-50">
      <SidebarProvider>
        <Sidebar>
          <SidebarHeader>
             <div className="flex items-center ml-4 mr-6 space-x-2">
                <Logo />
                <span className="font-bold">DATGenie</span>
              </div>
          </SidebarHeader>
          <SidebarContent>
            <SidebarMenu>
              <SidebarMenuItem>
                <SidebarMenuButton isActive={activeTab === 'Header'} onClick={() => setActiveTab('Header')}>
                  <FileText/>
                  Header
                </SidebarMenuButton>
              </SidebarMenuItem>
              <SidebarMenuItem>
                <SidebarMenuButton isActive={activeTab === 'Template'} onClick={() => setActiveTab('Template')}>
                  <LayoutTemplate/>
                  Template
                </SidebarMenuButton>
              </SidebarMenuItem>
              <SidebarMenuItem>
                <SidebarMenuButton isActive={activeTab === 'Convert'} onClick={() => setActiveTab('Convert')}>
                  <ChevronsRightLeft/>
                  Convert
                </SidebarMenuButton>
              </SidebarMenuItem>
              <SidebarMenuItem>
                <SidebarMenuButton isActive={activeTab === 'Forms'} onClick={() => setActiveTab('Forms')}>
                  <Folder/>
                  Forms
                </SidebarMenuButton>
              </SidebarMenuItem>
              <SidebarMenuItem>
                <SidebarMenuButton isActive={activeTab === 'DAT Files'} onClick={() => setActiveTab('DAT Files')}>
                  <History/>
                  DAT Files
                </SidebarMenuButton>
              </SidebarMenuItem>
            </SidebarMenu>
          </SidebarContent>
        </Sidebar>

        <div className="flex flex-col flex-1">
          <header className="sticky top-0 z-40 w-full border-b bg-white">
            <div className="flex items-center h-16 px-4">
              <div className="flex items-center">
                <SidebarTrigger />
              </div>
              <div className="flex items-center justify-end flex-1 space-x-4">
                <nav className="flex items-center space-x-1">
                   <Button variant="ghost" size="icon" onClick={handleRefresh}>
                    <RefreshCw className="h-4 w-4" />
                    <span className="sr-only">Refresh</span>
                  </Button>
                  <Button asChild variant="ghost">
                    <Link href="/login">
                      <LogOut className="mr-2" /> Logout
                    </Link>
                  </Button>
                </nav>
              </div>
            </div>
          </header>

          <SidebarInset>
            <main className="flex-1 bg-background">
              {renderContent()}
            </main>
            <footer className="py-6 border-t bg-white md:py-0">
              <div className="px-4 flex flex-col items-center justify-between gap-4 md:h-24 md:flex-row">
                <p className="text-sm text-center text-muted-foreground md:text-left">
                   {new Date().getFullYear()} DATGenie. All Rights Reserved. Designed and Developed by Christian Baccay
                </p>
              </div>
            </footer>
          </SidebarInset>
        </div>
      </SidebarProvider>
    </div>
  );
}
